.cart {
  //  display: flex;

  &__row {

    // display: flex;
    &--top-message {}

    &--product-price {}
  }

  &__flex {
    display: flex;
    justify-content: space-around;

    @include tablet{
      display: block;
    }
  }

  &__title {
    margin-top: 30px;
    text-align: center;
  }

  &__product-count {
    text-align: center;
  }
}

.productt {
  box-shadow: 0px 0px 39px 5px rgba(0, 0, 0, 0.17);
  border-radius: 15px;
  display: flex;
  // width: 100%;
  margin-left: auto;
    margin-right: auto;
  max-width: 37.5rem;
  margin-top: 20px;
  
  @include mobile{
    display: block;
  }

  &__photo {}

  &__photo--top {
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    width: 100%;

    @include mobile{
      border-bottom-left-radius: 0px;
      border-top-right-radius: 15px;
    }
  }

  &__cardd {
    padding: 20px;
    @include mobile{
      padding: 25px;  
  }
  }

  // Notă: Există o greșeală în denumirea clasei ("cardd" în loc de "card")
  &__name {
    margin-top: 5px;
    font-weight: bold;
  }

  &__details {
    margin-top: 10px;
  }

  .quantity {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
  }

  &__quantity-selector {
   

    &__button {
      &--decrease {}

      .mobile{
        width: 20px;
      }
      &--increase {}
    }

    &__button-remove {}
  }

  &__quantity-input {
    width: 30px !important;
  }
}

.delivery-methods {
  margin-top: 30px;

  &__title {
    text-align: center;
    font-weight: 700;
  }

  &__options {
    margin-top: 2rem;
    display: flex;
    gap: 1rem;
    justify-content: space-around;

    
  }

  &__option {
    display: flex;
    width: 40%;
    gap: 1rem;
    padding: 15px;
    border: 0.5px solid gray;
    border-radius: 15px;
    align-items: center;

    @include tablet{
      width: 50%;
    }

    @include mobile{
      gap: 0rem;  
      display: block;
  }
  }

  &__input {}

  &__label {}

  &__info {
display: flex;

@include mobile{
  display: block; 
}
     }
    &__icon {
      margin-top: 10px;
      margin-left: 10px;
      align-content: center;
      @include tablet{
        margin-top: 5px;
        margin-left: 5px;
      }
      
      &--sleep {
        // background-image: url("car2.svg");
        width: 70px;
        // height: 40px;
        @include tablet{
          width: 90px;
          margin-top: 15px;
        }
        @include mobile{
          margin: auto;
          width: 60px;
          margin-bottom: 10px;
      }

        

      }

      &--pickup {
        width: 55px;

        @include tablet{
          width: 80px;
        }

        @include mobile{
          margin: auto;
          width: 50px;
          margin-bottom: 20px;  
      }

      }
    }
 
    &__text {

      align-items: center;
      margin-left: 30px;
      display: grid;  
      @include mobile{
          text-align: center;
          margin-left: -5px;
      }
      &__option-title {}

      &__option-description {

      }
    }
  
}

.total {
  margin-top: 30px;
  width: 25%;

  @include tablet{
    width: 100%;
 }

  @include mobile{
     width: 100%;
  }
}

.button {
  &--primary {
    
    @include mobile{
      margin-top: 10px;
    }
  }

  &--secondary {}
