.containerr {
    max-width: calc(var(--container-width) + var(--container-padding-x) * 2);
    margin-inline: auto;
    padding-inline: var(--container-padding-x);
  }

.hidden-mobile{
    @include mobile{
        display: none!important;
    }
}
.visible-mobile{
    @include mobile-above{
        display: none!important;
    }
}