
.policy {
  p{
    font-weight: 300;
    line-height: 1.75rem;
  }
    padding: 20px;
    margin: 0 auto;
  
    &__title {
      margin-bottom: 20px;
    }
  
    &__section {
      margin-bottom: 40px;
      width: 100%;
      // padding: 20px;
      // background-color: #f5f5f5;
      border-radius: 8px;
      // box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  
      &__subtitle {
        margin-bottom: 10px;
        font-weight: 700;
      }
  
      &__paragraph,
      &__list-item {
        margin-bottom: 10px;
      }
  
      .policy__list {
        padding-left: 20px;
        list-style: inside;

        .policy__list-item{
            margin-top: 5px;
            font-weight: 300;
            line-height: 1.75rem;
        }
      }
    }
  }
  .last{
    // padding-top: 15px;
    color: var(--color-orange);
    font-weight: 500!important;
  }
  .subtitle{
    font-weight: 700;
  }