.main {
    background-image: url("banner2.png");
    // filter: grayscale(0%);
    // -webkit-filter: blur(0px); /* Safari 6.0 - 9.0 */
    // filter: blur(0px);   
    background-repeat: no-repeat;
    // background-size: 100%;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 92vh;
    margin-top: -100px;

    @include desktop {
        height: 70vh;
        background-size: cover;
    }

    @include tablet {
        margin-top: 0px;
        height: 50vh;
        background-size: cover;
    }

    @include mobile {
        //     margin-top: 0px;
        //     height: 55vh;
        height: 37vh;
        background-size: cover;
    }
}

.hero {

    &__title {
        padding-top: 12rem;
        color: var(--color-light);
        font-weight: 700;
        text-align: center;

        @include tablet {
            padding-top: 3.5rem;
        }

        @include mobile {
            padding-top: 2.5rem;
        }
    }

    &__subtitle {
        text-align: center;
        color: var(--color-light);
        font-weight: 300;
    }

    &__btn {
        padding: 10px;
        display: flex;
        position: absolute;
        margin-top: 40vh;
        left: 50%;
        transform: translateX(-50%);
        gap: 20px;
        border-bottom: 3px solid var(--color-orange);
        
        @include tablet{
            margin-top: 30vh;
        }


        @include mobile{
            margin-top: 20vh;
        }

        .arrbtn {
            height: 17px;
        }

        // @include desktop{
        //     margin-top: 22%;
        // }


    }

    &__cta {
        display: flex;
        color: var(--color-light);
        font-weight: 700;
        gap: 20px;


        // :hover{
        //         color: var(--color-orange) !important;
        //     }
    }



}