main{
    min-height: 47vh!important;
}
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    // min-height: 70vh;
    // margin-top: 10%;
    margin-top: 4rem;
    // margin-bottom: -10rem;
    background-color: #f0f2f5;
  }
  
  .login-form {
    width: 100%;
    max-width: 435px;
    padding: 30px;
    background: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    text-align: center;
  }
  
  .login-form__title {
    margin-bottom: 20px;
    font-size: 1.8rem;
    color: #333;
  }
  
  .login-form__group {
    margin-bottom: 15px;
  }
  
  .login-form__group input {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
    font-size: 1rem;
  }
  
  .login-form__forgot-pass {
    padding-top: 10px;
    padding-bottom: 20px;
    // margin: 10px 0;
    color: var(--color-dark);
    cursor: pointer;
  }
  
  .login-form__submit {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: var(--color-orange);
    color: white;
    font-size: 1rem;
    cursor: pointer;
  }
  
  /* Media queries for responsive design */
  @media (max-width: 768px) {
    .login-form {
      padding: 15px;
    }
  }
  