.product {
    &__related-title {
        font-weight: 700;
        font-size: 20px;
        margin: 20px 0px;
    }

    &__related-list {
        display: flex;
        flex-wrap: wrap;
        gap: 15px;

        @include mobile{
            justify-content: center;
        }
    }

    &__card {
        box-shadow: 0px 0px 39px 5px rgba(0, 0, 0, 0.17);
        width: 20rem;
        border-radius: 15px;
        display: flex;
        flex-wrap: wrap;
        img {
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;
        }
        @include mobile{
            width: 86%;
        }
    }

    &__card-body {
        margin-top: 15px;
    }

    &__card-title {
        font-weight: 700;
        font-size: 18px;
        text-align: center;
        margin-left: 10px;
    }

    &__card-features {
        margin-left: 5px;
        margin-top: 10px;
        text-align: left;
        font-weight: 300;
        font-size: 14px;
    }

    &__card-price {
        margin-left: 20px;
        text-align: left;
        font-size: 18px;
        margin-bottom: 15px !important;
    }
}