.contact{   
    margin-top: 50px;

    &__text-middle{
        text-align: center;
        font-weight: 300;
        line-height: 1.75rem;
    }

    &__title-middle{
        margin-top: 30px;
        
    }

    &__list-middle{
        font-weight: 300;
        line-height: 1.75rem;
    }

   &__form{
    margin: 10%;
   } 
    &__body{
        
        padding-bottom: 30px;
    }

    &__field{
        display: flex;
        flex-direction: column;
        width: 70%;

        .field__label{
            padding-top: 10px;
            padding-bottom: 5px;
            // padding-left: 10px;
        }

        .field__input{
            border-radius: var(--border-radius-small);
            padding: 12px;
            border: 0px;
            resize: none;
        }
    }

}