.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  
  
  &__content {
    background: #fff;
    padding: 20px;
    border-radius: 5px;
    max-width: 600px;
    width: 100%;
    z-index: 1001;
    li{
      font-size: 14px;
      font-weight: 300;
      line-height: 1.5;
    }
  }
  
  &__close {
    margin-top: -5px;
    font-size: 24px;
    float: right;
    cursor: pointer;
    color: var(--color-orange);
  }
}