.product {
    margin-top: 30px;

    &__row {
        
        display: flex;
        justify-content: space-around;

        &__image-section {


            width: 300px;
        }
        @include mobile{
            display: block;
        }
    }

    &__image {
        width: 600px;
        height: 450px;
        border-radius: 15px;
        @include mobile{
            margin-left:7px;
            width: 97%;
            height: auto;
            margin-top: 10px;
        }
        
    }

    &__information {
        background-color: var(--color-light);
        border-radius: 15px;
        width: 50%;


        @include desktop{
            margin-left:15px;
            font-size:12px;
        }
        @include mobile{
            margin-top: -30px;
            margin-left:7px;
            width: 97%;
        }
    }

    &__title {
        margin-top: 3rem;
        text-align: center;
        font-weight: 700;
        @include mobile{
            padding-top: 30px;
           
        }
    }

    &__category {
        margin-top: -15px;
        text-align: center;
        font-weight: 300;
        @include mobile{
            padding-top: 10px;
           
        }
    }

    &__size-quantity {
        margin-top: 30px;
        gap:10px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
        /* sau orice culoare de fundal necesară */
        padding: 10px;
        border-radius: 10px;
        margin-bottom: 20px;

        input {
            width: 23px;
        }
        @include mobile{
            margin-top: 10px;
            
        }
    }

    &__size-selector {
        display: flex;
        align-items: center;
    }

    &__size-label {
        margin-right: 10px;
        /* spațiu între etichetă și selector */
    }

    &__size-select {
        border: 0px solid #ccc;
        /* bordură subțire pentru selector */
        border-radius: 10px;
        /* rotunjirea colțurilor pentru selector */
        padding: 10px 20px 10px 10px;
        /* padding în interiorul selectorului */
        cursor: pointer;
        /* schimbă cursorul când e deasupra selectorului */
        background-color: var(--color-background);
        text-align: center;
        -webkit-appearance: none; /* Pentru Safari */
  -moz-appearance: none;    /* Pentru Firefox */
  appearance: none;         /* Standard pentru celelalte browsere */
  background-image: url('./arr.svg');
  background-size: 10px;
  background-repeat: no-repeat;
  background-position: right 5px center;   
  
  cursor: pointer;
  background-color: var(--color-background);
  text-align: center; 
  @include mobile{
    height: 40px;
  }
}

    &__quantity-selector {
        display: flex;
        align-items: center;
        // margin-left: 10px;
        background-color: var(--color-background);
        border-radius: 10px;
        padding: 5px;
    }

    &__quantity-btn {
        background-color: transparent;
        /* fundal transparent pentru butoane */
        border: none;
        /* elimină bordura implicită */
        color: #FFA500;
        /* schimbă culoarea textului */
        cursor: pointer;
        /* schimbă cursorul când e deasupra butonului */
        width: 20px;
        /* lățimea butonului */
        height: 30px;
        /* înălțimea butonului */
        font-size: 20px;
        
    }

    &__quantity-input {
        border: none;
        /* elimină bordura implicită */
        text-align: center;
        /* aliniere text în centru */
        margin: 0 20px;
        /* spațiu între butoane și input */
        width: 30px;
        /* lățimea maximă a input-ului */
        background-color: var(--color-background);
        @include mobile{
            width: 30px!important;
          }
    }

    &input[readonly] {
        // background-color: #f3f3f3; /* fundal mai închis pentru input readonly */
        cursor: not-allowed;        /* indică faptul că input-ul nu poate fi editat */
        width: 30px;
    }

    &__delivery-options {
        margin: auto;
        background-color: var(--color-background);
        width: 83%;
        padding: 15px;
        border-radius: 15px;
        align-items: center;
        // @include desktop{
        //   margin-left: 15px;
        // }
        @include mobile{
            width: 90%;
        }
        
    }

    &__delivery-options-item {
        display: flex;
        gap: 30px;
        text-align: center;
        align-items: center;
        margin-left: 35px;
        margin-top: 10px;
        margin-bottom: 10px;
        @include desktop{
            margin-left: 15px;
            gap:10px
          }

        @include mobile{
            gap: 5px;
            font-size: 11px;
        }
    }

    &__delivery-options-icon {
        width: 35px;
        @include mobile{
            margin-left: -10px;
        }
    }
    &__delivery-options-status-con{
        color: var(--color-orange);
    }

    &__price-row {
        margin-top: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 30px;
        @include mobile{
            padding-bottom: 20px;
            
        }
    }

    &__price {
        margin-top: 1rem;
        font-weight: 700;
    }

    // &__add-to-cart-btn {}

    &__size-individual{
        display: flex;
        flex-grow: 1;
        justify-content: center;
        gap: 20px;
    }

    &__size-individual-image{
        width: 15px;
    }

    &__size-individual-title{
        font-weight: 300;
        font-size: 14px;
    }

    &__title-compart {
        text-align: center;
        // margin-left: 30px;
        margin-top: 30px;
        font-size: 20px;
    }

    &__description {
        font-weight: 300;
        margin-left: 30px;
        margin-right: 23px;
        text-justify: inter-word;
        line-height: 1.5;

        @include mobile{
            font-size: 12px;
        }
    }

    &__specification-section {
        margin-top: 30px;
        background-color: var(--color-light);
        border-radius: 15px;
        padding: 20px 30px;
    }

    &__specification-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-radius: 15px;
    }

    &__specification-title {
        font-weight: 500;
        flex-grow: 1;
        @include mobile{
            font-size: 14px;
        }
    }

    &__specification-toggle {
        color: var(--color-orange);
        font-size: 1.5rem;
    }

    &__specification-list {
        margin-top: 20px;
        font-weight: 300;
    }

    &__specification-item {
        list-style-type: disc;
        margin-bottom: 10px;
        margin-left: 30px;
        margin-right: 40px;
        line-height: 1.5;
        @include mobile{
            margin-left: 5px;
           
        }
    }

    &__specification-table {
        border-radius: 10px;
        margin: auto;
        width: 80%;
        border-collapse: collapse;
        margin-top: 20px;
        background-color: var(--color-background);
        /* fundal gri deschis, ajustează culoarea conform design-ului tău */
        margin-bottom: 10px;
        @include mobile{
            width: 90%;
           
        }
    }

    &__specification-td {
        padding: 10px;
        border-bottom: 1px solid hsl(0, 0%, 100%);
        font-weight: 300;
        /* linie subțire între rânduri */
        font-size: 14px;
        @include mobile{
            font-size: 12px;
           
        }
    }

    &__specification-td--key {
        font-weight: 300;
        padding: 15px;
        /* font mai gros pentru chei */
        padding-left: 20px;
    }

    &__disclaimer {
        margin-top: 30px;
        font-weight: 500;
        margin-left: 30px;
        color: var(--color-orange);
        line-height: 1.5;
        @include mobile{
            font-size: 12px;
           
        }
    }
}






.row{
    width: 99%;
}