.catalog {
    margin-top: 130px;
    margin-bottom: 50px;
    padding: 0px 20px 0;

    @include mobile{
        margin-top: 50px;

    }

    &__inner {
        background-color: var(--color-light);
        display: flex;
        justify-content: space-around;
        border-radius: var(--border-radius-large);

        @include mobile {
            display: block;
        }
    }

    &__item {
        margin-top: 30px;
        margin-bottom: 30px;
        width: calc(100%/5.3);
        // column-gap: 30px;
        padding-top: 30px;
        padding-bottom: 30px;

        @include tablet{
            margin-top: 0px;
            margin-bottom: 0px;
            padding-top: 30px;
        padding-bottom: 30px;
        }

        @include mobile {
            width: 100%;
            margin: auto;
           
        }
    }

    &__image {

        .imagecatalog {
            margin: auto;
            width: 95%;
            border-radius: var(--border-radius-small);
        }
    }

    &__link {
        margin-top: 10px;
        text-align: center;

        @include tablet{
            font-size: 14px;
        }
    }
}