.mobile-overlay {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 14px 20px;
    border: none;
  
    &:modal {
      max-width: 100%;
      max-height: 100%;
    }
  
    &__close-button-wrapper {
      display: flex;
      justify-content: end;
    }
  
    &__list {
      @include flex-center;
  
      flex-direction: column;
      row-gap: 30px;
      min-height: 80vh;
    }
  }