.button{
    @include flex-center(true);
    border: 0px solid black;
    height: var(--button-height);
    padding-inline: 32px;
    font-size: 16px;
    color: var(--color-dark);
    background-color: var(--color-orange);
    border-radius: var(--border-radius) ;

    @include hover{
        opacity: 0.8;
    }
    @include desktop{
        font-size: 14px;
        padding-inline:16px;
    }
    @include tablet{
        font-size: 12px;
        padding-inline:10px;
    }
}
