.banner {

    display: flex;
    margin-bottom: 50px;

    @include mobile {
        display: block;
    }

    &__left {
        width: 50%;
        height: auto;

        @include mobile {
            width: 100%;
        }
    }

    &__right {
        width: 50%;
        background-color: var(--color-green);
        

        @include mobile {
            width: 100%;
            padding-bottom: 50px;
        }
    }

    &__right-item {
        margin-top: 60px;

        @include desktop {
            margin-top: 20px;
        }

        @include tablet {
            margin-top: 0;
        }
    }

    &__right-item-title {
        text-align: center;

        h1 {
            font-weight: 700;
        }

        @include mobile {
            padding-top: 30px;
        }
    }

    &__right-item-alldescription {
        margin-left: 60px;
        margin-right: 50px;
        margin-top: 60px;

        @include desktop-lx {
            margin-top: 30px;
        }

        @include desktop {
            margin-top: 10px;
            margin-left: 25px;
            margin-right: 25px;
        }

        @include tablet {
            margin-top: 0;
            margin-left: 0px;
            margin-right: 0px;
        }

        @include mobile {
            margin-top: 30px;
            margin-left: 20px;
            margin-right: 20px;
        }

    }

    &__right-item-subtitle {
        margin-top: 40px;

        @include desktop-lx {
            margin-top: 10px;
        }

        @include desktop {
            margin-top: 10px;
        }

        @include tablet {
            font-size: 12px;
        }

        @include mobile {
            font-size: 14px;
            margin-top: 15px;
        }
    }

    &__right-item-subtitle-descrition {
        font-weight: 300;
        margin-top: 10px;
        text-justify: auto;

        @include desktop {
            font-size: 14px;
        }

        @include tablet {
            font-size: 12px;
            margin-top: 0;
        }

        @include mobile {
            margin-top: 10px;
            font-size: 14px;
        }
    }
}