$desktop-above-lx: 1481px;
$desktop-lx: 1480px;

$desktop-above: 1281px;
$desktop: 1280px;

$tablet-above: 1024px;
$tablet: 1023px;

$mobile-above: 768px;
$mobile: 767px;

@mixin desktop-above-lx{
    @media(min-width: $desktop-above-lx){
        @content;
    }
}

@mixin desktop-lx{
    @media(max-width: $desktop-lx){
        @content;
    }
}

@mixin desktop-above{
    @media(min-width: $desktop-above){
        @content;
    }
}

@mixin desktop{
    @media(max-width: $desktop){
        @content;
    }
}

@mixin tablet-above{
    @media(min-width: $tablet-above){
        @content;
    }
}

@mixin tablet{
    @media(max-width: $tablet){
        @content;
    }
}

@mixin mobile-above{
    @media(min-width: $mobile-above){
        @content;
    }
}

@mixin mobile{
    @media(max-width: $mobile){
        @content;
    }
}

@mixin hover{
    @media (hover:hover){
        &:hover{
            @content;
        }
    }
    @media (hover:none){
        &:active{
            @content;
        }
    }
}