.benefits {
    padding-left: var(--container-padding-x);
    padding-right: var(--container-padding-x);

    &__inner {
        display: flex;
        justify-content: space-between;
        background-color: var(--color-light);
        border-radius: var(--border-radius-large);

        @include mobile {
            display: block;
            margin: auto;
            padding-top: 50px;
            padding-bottom: 50px;
        }
    }

    &__inner-item {
        width: 33%;
        margin-top: 50px;

        @include mobile {
            width: 100%;
            margin: auto;

        }
    }

    &__item-image .benefits-image {
        margin: auto;
        margin-bottom: 15px;

    }

    &__item-title {
        text-align: center;
        margin-bottom: 15px;

        p {
            font-weight: 700;
            color: var(--color-orange);
        }
    }

    &__item-description {
        width: 70%;
        text-align: center;
        margin: auto;
        font-weight: 300;
        margin-bottom: 30px;
        font-size: 14px;

        @include desktop {
            font-size: 14px;
        }
    }
}