.footer {
    background-color: var(--color-green);
    margin-top:50px;

    &__logo {
        margin: auto;
        margin-left: 0;
        @include desktop{
            margin:auto;
        }
        @include mobile {
            margin-top: 10px;
            img {
                padding-top: 30px;
                margin: auto;
                padding-left: 12px;
            }
        }
    }

    &__inner {
        display: flex;
        flex-direction: row;

        @include mobile {
            display: block;
        }
    }

    &__menu {
        margin-top: 50px;
        display: flex;
        flex-direction: row;
        column-gap: 4rem;
        margin-left: 100px;

        @include desktop {
            margin-right: 100px;
            column-gap: 2rem;
        }

        @include tablet {
            margin-left: 0;
            margin-right: 0;
        }

        @include mobile {
            display: block;
            text-align: center;
        }

        &-item-title {
            font-size: 14px;
            font-weight: 700;
            margin-bottom: 10px;

            @include desktop {
                font-size: 14px;
            }

            @include tablet {
                font-size: 12px;
            }
        }

        &-list {
            margin-bottom: 10px;
            font-weight: 300;
            font-size: 14px;

            @include desktop {
                font-size: 12px;
            }

            @include tablet {
                font-size: 12px;

            }
        }
    }

    &__privacy {
        margin-top: 20px;
        padding-top: 20px;
        padding-bottom: 20px;
        text-align: center;
        font-weight: 300;
        background-color: rgba(0, 0, 0, 0.06);
        p{
            margin-bottom: 0;
        }

        @include desktop {
            font-size: 14px;
        }
    }
}