.navbar{
    background-color: rgba(212, 212, 212, 0.3  )    !important;
    // opacity: 0.2;
    height: 5rem;
    z-index: 1000;

    @include tablet{
        height: 3.9rem;
        background-color: rgba(254, 254, 254  )    !important;
    }
    &-brand {
        width: 130px;

        .imagelogo{
            margin-top: -10px;
            width: 110px;
        }
    }
    &-nav{
        align-items: center;
        gap: 4rem;
        margin-left: 4rem;
        
        @include tablet{
            margin-top: 2rem;
            gap: 2rem;
        margin-left: 0rem;
        }
    }
}
.collapse{
    // align-items: center;
    justify-content: center;
    align-items: baseline;
    // gap: 40px;

    .show{
        // height: 300px;
       
        ul li .nav-link{
            color: var(--color-dark);
        }
    }
}
    .dropdown-toggle .active .show{
        height: 60px;
    }
.cartimage{
    width: 16px;
    // fill: var(--color-light);
}
.elements{
    z-index: 1;
    @include tablet{
        background-color: rgba(254, 254, 254, 1 )    !important;
    }
   
}
.ant-scroll-number{
    background-color: var(--color-orange)!important;
    margin-top: -6px!important;
    margin-right: -6px!important;
    
}
.ant-badge-count{
    box-shadow: 0 0 0 0 !important;
}
.nav-item{
    list-style: none;
}
.nav-link{
    color: var(--color-dark);
    font-weight: 500;
}

.nav-item-card{
    list-style: none;
    // color: antiquewhite;

}

 .dropdown{
    list-style: none;
}

.right-nav{
    display: flex;
    justify-content: end;
    gap: 40px;
    margin-left: 80px;
    text-align: center;

    @include tablet{
        padding-top: 2rem;
        padding-bottom: 50px;
        display: grid;
        justify-content: center;
        margin-left: 0px;
    }
}

.language{
    --bs-dropdown-min-width: 3.5rem; 

}

.langbtn{
    background-color: transparent;
    border: 0px;

    
}

  
  .langbtn.active {
    
    color: var(--color-orange);
  }