.filter {
    // display: block;

    // @include mobile{
    //     display: flex;
    // }

    &__sidebar {
        width: 300px;
        background-color: var(--color-light);
        /* height: 100vh; */
        padding-top: 30px;
        padding-bottom: 30px;
        padding-left: 30px;
        border-bottom-right-radius: 15px;
        border-top-right-radius: 15px;
        -webkit-box-shadow: 4px 4px 24px 0px rgba(0, 0, 0, 0.26);
        box-shadow: 4px 4px 24px 9px rgba(0, 0, 0, 0.08);

        @include mobile{
           margin: auto;
            border-radius: 15px;
            width: 86%;
            // display: flex;
        }
    }

    &__heading {
        font-weight: 700;
    }

    &__heading-weight {
        margin-top: 20px;
    }

    &__categories {
        display: grid;

        // @include mobile{
        //     display: flex;
        //     justify-content: space-around;
        // }
    }
    // .filter-sidebar__radio-group{
    //     @include mobile{
    //         display: flex;
    //         justify-content: space-around;
    //         text-align: center;
    //     }
    // }

    &__checkbox {
        font-size: 14px;

        span {
            font-weight: 500;
            font-family: var(--font-family-base);
        }
    }


    &__actions {
        margin-top: 20px;
        // width: 30%;
    }

    &__button {
        @include flex-center(true);
        border: 0px solid black;
        height: var(--button-height);
        padding-inline: 32px;
        font-size: 14px;
        color: var(--color-dark);
        background-color: var(--color-orange);
        border-radius: var(--border-radius);

        @include hover {
            opacity: 0.5;
        }

        @include desktop {
            font-size: 14px;
            padding-inline: 16px;
        }

        @include tablet {
            font-size: 12px;
            padding-inline: 16px;
        }
    }

}