.delivery{
    margin-top: 40px;
  
    &__title h1{
        font-weight: 700;
    }
    

    &__description-title{
        margin-top: 15px;
        margin-bottom: 10px;
    }

    &__description-title-desc{
        font-weight: 300;
        text-align: justify;
        line-height: 1.75rem;
        list-style-type: disc;
        list-style-position: inside;

        @include mobile{
            font-size: 14px;
            margin-bottom: 10px;
        }
    }
}