.custom-dashboard-container {
    padding: 1rem;
    max-width: 1200px;
    margin: auto;
    
    .custom-dashboard-row {
      display: flex;
      flex-wrap: wrap;
    }
  
    .custom-dashboard-main {
      flex: 1;
      padding: 1rem;
    }
  
    .custom-card {
      background-color: #f8f9fa;
      padding: 1rem;
      margin-bottom: 1rem;
      border-radius: 4px;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  
      h3 {
        margin-bottom: 0.5rem;
      }
    }
  
    .custom-form-container {
      display: flex;
      justify-content: center;
    }
  
    .custom-form {
      width: 100%;
      max-width: 600px;
      background-color: #fff;
      padding: 2rem;
      border-radius: 4px;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  
      .custom-title {
        margin-bottom: 1rem;
        text-align: center;
      }
  
      .custom-form-group {
        margin-bottom: 1rem;
  
        .custom-input {
          width: 100%;
          padding: 0.75rem;
          border: 1px solid #ced4da;
          border-radius: 4px;
          margin-bottom: 0.5rem;
  
          &:focus {
            border-color: #80bdff;
            outline: 0;
            box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
          }
        }
      }
  
      .custom-button {
        width: 100%;
        padding: 0.75rem;
        color: #fff;
        // background-color: #007bff;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s;
  
        &:hover {
          background-color: #0056b3;
        }
      }
    }
  }
  
  @media (max-width: 768px) {
    .custom-dashboard-container {
      .custom-dashboard-main {
        padding: 0;
      }
      .custom-form {
        padding: 1rem;
      }
    }
  }
  