.warranty {
    p{
        font-weight: 300;
        line-height: 1.75rem;
    }
    padding-top: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    width: 100%;
    max-width: 1200px;
  
    &__title,
    &__manufacturer-guarantee-header,
    &__exclusions-header,
    &__mattress-use-header {
      margin-top: 15px;
      margin-bottom: 10px;
    //   text-align: center;
    font-weight: 700;
    }
  
    &__introduction,
    &__commitment,
    &__important-notice,
    &__manufacturer-guarantee-life-span,
    &__manufacturer-guarantee-compliance,
    &__manufacturer-guarantee-warranty-service,
    &__manufacturer-guarantee-replacement,
    &__exclusions-notification,
    &__exclusions-address,
    &__i-sleep-statement {
      margin: 10px 0;
      font-size: 1rem;
      text-align: justify;
    }
  
    &__top-section {
      display: flex;
      justify-content: space-between;
      width: 100%;
      gap: 20px; // Adds space between text and image
      
      @include tablet{
        flex-direction: column;
        align-items: center;
      }
    }
  
    &__customer-rights,
    &__customer-left,
    &__manufacturer-guarantee,
    &__exclusions,
    &__mattress-use {
     @include tablet{
        width: 100%;
      }
    }
  
    &__list {
      list-style: disc outside none;
      padding-left: 40px;
    }
  
    &__list-item {
      margin-bottom: 5px;
      padding-bottom: 5px;
      font-weight: 300;
      line-height: 1.75rem;
    }
  
    &__customer-left {
      // Styling for the image container
      flex-shrink: 0;
      margin-right: 20px; // Adjust as needed to bring text closer to the image
      img {
        margin-top: 30px;
        width: 100%; // Adjust if needed
        max-width: 300px; // Adjust the image max-width as needed
        height: auto;

        @include tablet{
            margin: auto;
        }
      }
    }
  }
  
